  max-width: 100%;
  height: 100%;
}

.red {
  color: #c8181a;
}

.grey {
  color: #9c9c9c;
}

.head {
  display: flex;
  justify-content: center;
  border: 2px solid black;
}

.head .wrapper {
  width: 100%;
}

.head .wrapper .header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  background: white;
  margin-top: 61px;
}

.head .wrapper .header .h6 {
  margin: 0;
}

.head .wrapper .title {
  margin: 20.72387624051372% auto 20.72387624051372%;
}

.head .wrapper .title .h2 {
  margin: 0;
}

.head .wrapper .scroll {
  display: flex;
  justify-content: center;
  margin-bottom: 8.990075890251022%;
}

.head .wrapper .scroll .img {
  width: 18px;
  height: 37px;
}

.main .wrapper .h2 {
  margin: 61px 0 0;
  text-align: left;
}

.main .wrapper .ul {
  display: flex;
  margin-top: 59px;
}

.main .wrapper .ul .li {
  border: 1px solid #000;
  border-radius: 30px;
  padding: 20px 25px;
  margin-left: 18px;
}

.main .wrapper .ul .li:first-child {
  margin-left: 0;
}

.main .wrapper .carousel {
  display: flex;
  overflow-x: scroll;
  margin-top: 12px;
  margin-bottom: 108px;
}

.main .wrapper .carousel .item {
  margin-top: 60px;
  margin-bottom: 100px;
  flex: 0 0 477px;
  width: 477px;
  height: 477px;
  border: 1px solid green;
  margin-left: 49px;
  box-shadow: 0 0 25px 12px #dbdbdb;
}

.main .wrapper .carousel .item:first-child {
  margin-left: 0;
}

.main .wrapper .carousel .item:nth-child(4) {
  background: blue;
}

.main .wrapper .carousel .item:nth-child(5) {
  background: red;
}

.main .wrapper .carousel .item:nth-child(6) {
  background: green;
}

.footer {
  background: #f5f5f5;
  height: 100%;
  display: flex;
}

.footer .wrapper {
  margin-top: 226px;
}

.footer .wrapper .h2 {
  font-size: 34px;
  margin: 0;
}

.footer .wrapper .footer_info {
  display: flex;
  margin-top: 80px;
}

.footer .wrapper .footer_info .info .item {
  margin-top: 45px;
}

.footer .wrapper .footer_info .info .item:first-child {
  margin-top: 25px;
}

.footer .wrapper .footer_info .info .item .h5,
.footer .wrapper .footer_info .info .item .h6 {
  text-align: left;
  margin: 0;
  width: 100%;
}

.footer .wrapper .footer_info .info .item .h6 {
  font-size: 16px;
}

.footer .wrapper .footer_info .info .item .h5 {
  font-size: 15px;
}

.footer .wrapper .footer_info .border {
  width: 1px;
  background: #000;
  border: 0,5px solid black;
  margin: 0 96px;
}

.footer .wrapper .footer_info .feedback .item {
  margin-top: 45px;
}

.footer .wrapper .footer_info .feedback .item:first-child {
  margin-top: 25px;
}

